import type { Agreement, CopyrightClaim } from "~/types/api"

export function getAgreements(params: { ids?: number[]; required_for?: "copyright_claims" } = {}) {
  return get<Agreement[]>("get_agreements", {
    params,
  })
}

export function submitCopyrightClaim(data: {
  url: string
  name?: string
  phone?: string
  email?: string
  license_text: string
  "g-recaptcha-response"?: string
}) {
  return post("submit_copyright_claim", {
    data,
  })
}

export function getCopyrightClaims() {
  return get<CopyrightClaim[]>("get_copyright_claims")
}
